import classNames from 'classnames';
import React from 'react';

export interface DetailLayoutProps extends React.HTMLAttributes<HTMLDivElement> {
  readonly variant?: 'main' | 'mainAside';
}

export const DetailLayout: React.FC<DetailLayoutProps> = ({
  variant = 'main',
  className,
  children,
  ...props
}) => (
  <div
    className={classNames(
      'grid',
      {
        'grid-cols-detailLayout': variant === 'main',
        'grid-cols-detailLayoutAside': variant === 'mainAside',
      },
      className
    )}
    {...props}
  >
    {children}
  </div>
);
